<template>
  <div class="info-container">
    <!-- 标题 -->
    <el-row>
      <!-- <el-col :span="2"><i class="el-icon-back" style="font-size: 45px;line-height: 45px;"></i></el-col> -->
      <el-col :span="22"><h1 class="adopt-title">{{ adopt.title }}</h1></el-col>

    </el-row>
    <!-- 创建者 -->
    <el-row>
      <el-col :span="24">
        <div class="creator">
          <el-avatar :size="60" :src="adopt.creator.avatar"></el-avatar>
          <div class="creator-name">  by
            <a>{{ adopt.creator.nickname }}</a>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- 创建时间 -->
    <el-row style="margin-bottom: 20px;">
      <el-col :span="24">
        <div class="creator-time">
          <i class="el-icon-time" style="font-size: 18px;"></i>
          <div class="time">{{ adopt.createTime }}</div>
        </div>
      </el-col>
    </el-row>
    <!-- 宠物基本信息 -->
    <el-row>
      <!-- 封面图片 -->
      <el-col :span="8">
          <div style="width: 100%; height: 345px; overflow: hidden;">
            <el-image v-if="adopt.cover"
            style="width: 100%;"
            :src="adopt.cover"
            fit="cover"></el-image>
          </div>
      </el-col>

      <el-col :span="12">
        <!-- 基本信息（地址、年龄、是否驱虫、是否打疫苗、是否绝育、公母、） -->
        <el-row class="about-title" style="margin-left: 80px;margin-bottom: 20px;">
            <el-col :span="6"><h2>宠物基本信息</h2></el-col>
          </el-row>
        <div>
          <el-row class="about-info-row" style="margin-left: 80px;">
            <el-col :span="6"><h3>名称：</h3></el-col>
            <el-col :span="16"><p>{{ adopt.title }}</p></el-col>
          </el-row>
          <el-row class="about-info-row" style="margin-left: 80px;">
            <el-col :span="6"><h3>种类：</h3></el-col>
            <el-col :span="16"><p>{{ adopt.gender?adopt.petType +'（公）':adopt.petType +'（母）' }}</p></el-col>
          </el-row>
          <el-row class="about-info-row" style="margin-left: 80px;">
            <el-col :span="6"><h3>年龄：</h3></el-col>
            <el-col :span="16"><p>{{ '2年4个月' }}</p></el-col>
          </el-row>
          <el-row class="about-info-row" style="margin-left: 80px;">
            <el-col :span="6"><h3>地址：</h3></el-col>
            <el-col :span="16"><p>{{ adopt.region }}</p></el-col>
          </el-row>
          <el-row class="about-info-row" style="margin-left: 80px;">
            <el-col :span="6"><h3>健康信息：</h3></el-col>
            <el-col :span="16">
              <el-tag v-if="true" style="margin-right: 6px;" size="small" type="success">有疫苗</el-tag>
              <el-tag v-if="true" style="margin-right: 6px;" size="small" type="warning">已驱虫</el-tag>
              <el-tag v-if="true" size="small" type="danger">已绝育</el-tag>  
            </el-col>
          </el-row>
        </div>
        <!-- 领养按钮 -->
        <div style="margin-top: 30px;margin-left: 80px;">
          <el-button @click="showAdoptForm" class="adoptBtn" type="primary" style="background-color: #d5b48e; border-color: #C2A380;" round>申请领养</el-button>
          <el-button type="info" plain round>点击收藏</el-button>
        </div>
       

      </el-col>
    </el-row>
    <!-- about -->
    <div class="about">
      <el-row>
        <el-col :span="14">
          <el-row class="about-title">
            <el-col :span="24"><h2>关于 {{ adopt.title }}</h2></el-col>
          </el-row>
          <!-- <el-row class="about-info-row" v-for="(val, key, i) in adopt.about" :key="i">
            <el-col :span="8"><h3>{{ key }}</h3></el-col>
            <el-col :span="16"><p>{{ val }}</p></el-col>
          </el-row> -->
          <el-row class="about-info-row" v-for="(item, i) in adopt.about" :key="i">
            <el-col :span="8"><h3>{{ item.name }}</h3></el-col>
            <el-col :span="16"><p>{{ item.value }}</p></el-col>
          </el-row>
        </el-col>
        <el-col :span="7" >
          <div class="col-md-4 about-colorbar">
            <div class="row">
              <div id="rate-wrap" class="col-sm-11 col-md-10">
                <h2 class="mobmid">宠物属性 </h2>

                <div v-for="(item, index) in rateList" :key="index">
                  <p>{{ item.name }}</p>
                  
                    <div class="progress" slot="reference" @click="rateShow(index)" >
                      <el-rate class="rate-mark-row"
                      :max="10"
                      @change="markedRate(index)"
                      v-model="item.value" v-if="item.isShow"></el-rate> 
                      <div :class="'progress-bar progress-bar-'+ index" role="progressbar" aria-valuenow="65" aria-valuemin="0" aria-valuemax="100" :style="{'width': item.rate * 10 + '%'}">
                        <span class="sr-only">{{ item.rate * 10 }} % </span>
                        
                      </div>
                      <i class="el-icon-check mainright" style="line-height: 20px; margin-right: 12px;"></i>
                    </div>
                </div>
                
              </div><!--info end-->
            </div><!--tech skills end-->
          </div><!--right end-->
        </el-col> 
      </el-row>
      
    </div>

    <!-- 申请表单 -->
    <el-dialog
      title="填写申请领养表单"
      :visible.sync="isShowAdoptForm"
      width="32%"
      top="10vh"
      center>

      <el-form :model="adoptForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-row style="margin-top: 10px;">
          <el-col :span="22">
            <el-row>
              <el-col :span="20">
                <el-form-item class="topMargin" label="姓名：" prop="name">
                  <el-input v-model="adoptForm.name"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="20">
                <el-form-item class="topMargin" label="电话：" prop="phone">
                  <el-input v-model="adoptForm.phone"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="20">
                <el-form-item class="topMargin" label="微信：">
                  <el-input v-model="adoptForm.wechat"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="20">
                <el-form-item class="topMargin" label="QQ：">
                  <el-input v-model="adoptForm.qq"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item class="topMargin" label="现居地：" prop="region">
              <el-cascader
              size="large"
              :options="options"
              v-model="selectedOptions"
              @change="regionChange"
              :props="{ expandTrigger: 'hover' }">
              </el-cascader>
            </el-form-item>

            <el-form-item class="topMargin" label="描述：" prop="description">
              <el-input
                type="textarea"
                placeholder="描述寄养环境，以及家中宠物情况"
                v-model="adoptForm.description"
                maxlength="200"
                :rows="5"
                show-word-limit
              >
              </el-input>
            </el-form-item>


          </el-col>
        </el-row>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitAdpotForm">提 交</el-button>
        <el-button @click="isShowAdoptForm = false">取 消</el-button>
      </span>
    </el-dialog>

    <!-- 详细信息 -->
    <div class="detail" v-if="this.adopt.contentType=='JSON'">
      <el-row class="detail-title">
        <el-col :span="20"><h1>detail</h1></el-col>
      </el-row>
      <el-row class="detail-info-row" v-for="(val, key, i) in adopt.content" :key="i">
        <el-col :span="22">
          <h2 class="time">{{ key }}</h2>
          <p>{{ val }}</p>
        </el-col>
      </el-row>
    </div>

    <div class="detail" v-if="this.adopt.contentType=='HTML'">
      <el-row class="detail-info-row">
        <el-col class="detail-col" :span="22" v-dompurify-html="this.adopt.content">

        </el-col>
      </el-row>
    </div>

    <!-- 收藏按钮 评论按钮 转发按钮 -->
    <div class="btn-warp">
      <el-button plain v-if="!isCollection" @click="adoptCollection(true)" icon="el-icon-star-off">未收藏</el-button>
      <el-button plain v-if="isCollection" @click="adoptCollection(false)" icon="el-icon-star-on">已收藏</el-button>
      <el-button plain icon="el-icon-chat-dot-round">评论</el-button>
      <el-button plain icon="el-icon-edit-outline">反馈</el-button>

    </div>


    <!-- 作者其他百科 -->
    <el-empty description="作者其他百科"></el-empty>
    
    <!-- 相关推荐 -->
    <el-empty description="相关推荐"></el-empty>


  </div>

</template>

<script>
import { regionData, codeToText } from 'element-china-area-data'
import BootBar from '../index/BootBar.vue';
import * as apiAdopt from "@/api/adopt.js";
import * as apiRate from "@/api/rate.js";
import storage from '@/plugins/storage';
export default {
  components: {
    BootBar
  },
  props: {},
  data() {
    return {
      isCollection: false,
      adopt : {"id":"1712657059792601088","title":"苏格兰折耳猫","region":"北京市,市辖区,东城区","petType":"cat","cover":"http://s1osmcysm.hn-bkt.clouddn.com/adopt-cover/7/15/b47d82fe-3311-4b79-9a4a-8d7a1233787c.jpg","description":"The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!","about":[{"name":"Height","value":"12-14 inches"},{"name":"Weight","value":"7-17 lb"},{"name":"Physique","value":"Sturdy, big bodied cats"},{"name":"Lifespan","value":"8-10 years"},{"name":"Best Suited For","value":"Families and singles with or without children and pets"},{"name":"Temperament","value":"Calm, easy going, adaptable, affectionate"},{"name":"Comparable Breeds","value":"British Longhair, Ragamuffin"}],"content":"<p><span style=\"color: rgb(51, 51, 51);\">❤️最大特点是性格好，来家里的孩子都喜欢。</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅女猫6岁从未咬过人</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅疫苗接种</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅宠物驱虫</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅从0岁养到6岁，没换过主人</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅已绝育不吃植物，一般不和人睡，如果睡就是脚底下，不去床头胆小比较小，但喜欢找人玩，不一定适合和其他宠物同居；</span></p><p><span style=\"color: rgb(51, 51, 51);\">要看是否合得来由于工作原因陪猫玩的时间不够了，希望她能快乐点有心人可以私信沟通照片收养人真心爱猫，有长居住所，懂得如何养猫，给好心收养人赠送，希望对她好</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅自动喂粮器</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅电动饮水器</span></p><p><span style=\"color: rgb(51, 51, 51);\">✅电动猫盆子</span></p>","contentType":"HTML","createTime":"2023-10-13T15:08:35","updateTime":null,"adoptState":"UN_ADOPTED","isDewormed":true,"isVaccinated":true,"isSterilized":true,"gender":true,"creator":{"id":"1001","nickname":"AzMiao","avatar":"http://s1osmcysm.hn-bkt.clouddn.com/Avatar/15/12/f44b257f-c591-43f7-b170-fda675f2b6a3.jpg"}},
      rateList: [
        {
            "name": "kind",
            "rate": "4.00",
            "markCount": 1,
            "value": null,
            "isMarked": false,
            "isShow": false
        },
        {
            "name": "vitality",
            "rate": "6.00",
            "markCount": 1,
            "value": null,
            "isMarked": false,
            "isShow": false
        },
        {
            "name": "independent",
            "rate": "5.00",
            "markCount": 1,
            "value": null,
            "isMarked": false,
            "isShow": false
        },
        {
            "name": "iq",
            "rate": "4.00",
            "markCount": 1,
            "value": null,
            "isMarked": false,
            "isShow": false
        }
    ],

      isShowAdoptForm: false,
      // 地区级联选择
      options: regionData,
      selectedOptions: [],
      adoptForm: {
        adoptId: '',
        region: '',
        name: '',         // 姓名
        phone: '',        // 手机号
        qq: '',     // qq号
        wechat: '',   // 微信号
        description: '',   // 领养条件描述
      },

      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 30, message: '长度在 2 到 30 个字符', trigger: 'blur' }
        ],
        phone: [{ required: true, message: '请输入电话号码', trigger: 'blur' }],
        region: [{ required: true, message: '请选择现居地', trigger: 'change' }],
        description: [{ required: true, message: '请输入领养环境', trigger: 'blur' }],
      },
    }
  },
  watch: {},
  computed: {},
  created() {
  },
  mounted() {
    this.adopt.id = this.$route.params.adoptId;
    this.getAdoptData();
  },
  methods: {
    showAdoptForm() {
      this.isShowAdoptForm = true;
    },
    regionChange () {
      this.adoptForm.region = '';
      for (let i = 0; i < this.selectedOptions.length; i++) {
        this.adoptForm.region += codeToText[this.selectedOptions[i]];
          if (i < this.selectedOptions.length - 1) this.adoptForm.region += ','
      }
    },
    submitAdpotForm() {
      this.$refs.ruleForm.validate(async valid => {
        if (!valid){
          this.$message.error('请完善领养信息')
          return false
        } 
        this.adoptForm.adoptId = this.adopt.id;
        apiAdopt.submitAdoptForm(this.adoptForm).then(res=>{
          if(res.success){
            this.$message.success('申请成功，预计1个工作日内答复');
            // 跳转到我的领养申请信息页面
            this.isShowAdoptForm = false;
          }else{
            this.$message.error(res.message);
          }
        })
      })
    },
    adoptCollection(isCollection) {
      let params = {
          adoptId: this.adopt.id,
        }
        if (isCollection) {
          apiAdopt.adoptCollection(params).then((res)=>{
            if (res.success) {
              // this.adopt = res.result;
              this.$message.success(res.result);
              this.getAdoptCollection()
            } else {
              this.$message.error(res.message);
            }
          })
        }else {
          apiAdopt.adoptUnCollection(params).then((res)=>{
            if (res.success) {
              // this.adopt = res.result;
              this.$message.success(res.result);
              this.getAdoptCollection()
            } else {
              this.$message.error(res.message);
            }
          })
        }
    },
    getAdoptData(){
      apiAdopt.getAdoptData(this.adopt.id).then((res)=>{
        if (res.success) {
          this.adopt = res.result;
          this.adopt.about = JSON.parse(res.result.about)

        } else {
          this.$message.error(res.message);
        }
      })
      // this.getAdoptCollection()
      // 获取用户评分信息
      this.getWikiRate()
    },
    getAdoptCollection() {
      // 获取用户收藏信息
      if (storage.getItem("userInfo") != undefined) {
        let params = {
          adoptId: this.adopt.id
        }
        apiAdopt.getAdoptCollectionByAdoptId(params).then((res)=>{
          if (res.success) {
            this.isCollection = res.result;
          } else {
            this.$message.error(res.message);
          }
        })
      }
    },
    getWikiRate() {
      // 获取wiki宠物评分信息
      apiRate.getAdoptRate(this.adopt.id).then((res)=>{
        if (res.success) {
          this.rateList = res.result;
        } else {
          this.$message.error(res.message);
        }
      })
    },
    markedRate(index){
      // if(this.rateList[index].isMarked){
      //   return
      // }
      //判断用户登录
      if (storage.getItem('userInfo') != undefined){
        let params = {
          adoptId: this.adopt.id,
          name: this.adopt.rateList[index].name,
          value: this.adopt.rateList[index].value
        }
        apiAdopt.markedRate(params).then((res)=>{
          if (res.success) {
            this.$message.success("评分成功");
          } else {
            this.$message.error(res.message);
          }
        })
      }else {
        this.$message.error('用户未登录');
				this.$store.state.login.loginPageShow = true;	
      }
      

    },
    rateShow(index){
      this.adopt.rateList[index].isShow = true;
      return
    },
    rateDisShow(index){
      console.log('Leave' + index)
      this.adopt.rateList[index].isShow = false;
      return
    },
  },

};
</script >


<style lang="css" scoped>
.adoptBtn:hover {
  color: #ffffff;
  background-color: rgb(191, 148, 100) !important;
  border-color: #d5b48e !important;
  transition: all .3s;
}

.btn-warp {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  
}
.btn-warp button {
  margin: 0 20px;
}

.about-colorbar {
  width: 70%;
  float: right;
  margin-right: 10%;
}
.detail {
  margin-top: 50px;
}
.detail-title {
  margin-top: 20px;
}
.detail-info-row {
  margin-top: 20px;
}

.detail >>> p {
  margin-top: 10px;
  font-size: 19px;
}
.detail >>> h2{
    line-height: normal !important;
    margin-top: 20px;
    display: inline-block;
}
.detail h2 {
  line-height: normal !important;
  margin-top: 20px;
  display: inline-block;
}

.detail p {
  margin-top: 20px;
  font-size: 19px;
}

.about {
  margin-top: 20px;
}
.about-title {
  margin-bottom: 14px;
}

.about-info-row {
  margin-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #dce1e6;
}
.about-info-row p {
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6{
    font-family: Montserrat,sans-serif;
    font-weight: 600;
    line-height: 1.25em;
    color: #000;
}
.info-container {
    padding: 0% 4%;
    padding-top: 3%;    
}
.adopt-title {
  font-size: 36px;

}
.creator,
.creator-time {
  display: flex;
  justify-content: left;
  margin-top: 20px;
  
}
.creator-name {
  line-height: 60px;
  font-size: 16px;
  margin-left: 14px;
  
}
.creator-name a {
  cursor: pointer;
}
.creator-time .time {
  line-height: 18px;
  font-size: 14px;
  margin-left: 4px;
  color: #8f8f8f;
}

.about-colorbar .mainleft {
    float: left;
}
.about-colorbar .mainright {
    float: right;
}
.about-colorbar .mobmid {
    margin-bottom: 10px;
}
.about-colorbar p {
    margin: 0 0 4px;
}
.about-colorbar .progress {
    height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
    box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
    cursor: pointer;
}
.about-colorbar .progress-bar {
    float: left;
    width: 55%;
    height: 100%;
    font-size: 12px;
