    margin-right: 7px;
}

.about-colorbar .btn .fa {
    margin-right: 10px;
}

.about-colorbar .secicon.fa {
    font-size: 28px;
    color: #000;
    margin-right: 10px;
}

.about-colorbar .sub {
    margin-bottom: 15px;
}

.about-colorbar .award {
    padding-top: 20px;
}

